import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import { Routes, Route, Link, useParams } from "react-router-dom";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import { getColors, getAverage, plusSlides } from "../config/myfunctions";
import eventService from "../api/eventService";
import Styles from "../config/styles";
import Colors from "../config/colors";
import ChartjsBar from "../components/ChartjsBar";
import RadarGraph from "../components/RadarGraph";
import LegendLeaders from "../components/LegendLeaders";

const client = axios.create({
  baseURL: "http://192.168.0.171",
});

function Province(props) {
  const params = useParams();
  const pro = params.id;
  const [projects, setProjects] = useState([]);
  const [province, setProvince] = useState();
  const [labels, setLabels] = useState([]);
  const [values, setValues] = useState([]);
  const refs = useRef();
  refs.current = [];

  useEffect(() => {
    const getData = () => {
      eventService.maproutes
        .proincir(pro)
        .then((res) => {
          setProjects(res.data.results);
        })
        .catch((err) => {
          console.log(err);
        });
      eventService.maproutes
        .circumscription(pro)
        .then((res) => {
          setProvince(res.data.name);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    getData();
    async function getLabels() {
      const res = await client.get("/labels");
      const lblpart = [];
      const lbledu = [];
      [...Array(5)].forEach((_, i) => {
        lbledu.push(res.data[0].edu_labels[i].name);
      });
      const lblhea = [];
      [...Array(4)].forEach((_, i) => {
        lblhea.push(res.data[1].hea_labels[i].name);
      });
      const lbldev = [];
      [...Array(4)].forEach((_, i) => {
        lbldev.push(res.data[2].dev_labels[i].name);
      });
      const lbljpi = [];
      [...Array(5)].forEach((_, i) => {
        lbljpi.push(res.data[3].jpi_labels[i].name);
      });
      lblpart.push(lbledu);
      lblpart.push(lblhea);
      lblpart.push(lbldev);
      lblpart.push(lbljpi);
      setLabels(lblpart);
    }
    async function getValues() {
      const res = await client.get(`/projects/?circumscription=${pro}`);
      const valpart = [];
      const valedu = [];
      [...Array(5)].forEach((_, i) => {
        valedu.push(
          res.data.results.filter((pro) => pro.activity_education === i + 1)
            .length
        );
      });
      const valhea = [];
      [...Array(4)].forEach((_, i) => {
        valhea.push(
          res.data.results.filter((pro) => pro.activity_health === i + 1).length
        );
      });
      const valdev = [];
      [...Array(4)].forEach((_, i) => {
        valdev.push(
          res.data.results.filter((pro) => pro.activity_development === i + 1)
            .length
        );
      });
      const valjpi = [];
      [...Array(5)].forEach((_, i) => {
        valjpi.push(
          res.data.results.filter((pro) => pro.activity_jpi === i + 1).length
        );
      });
      valpart.push(valedu);
      valpart.push(valhea);
      valpart.push(valdev);
      valpart.push(valjpi);
      setValues(valpart);
    }
    getLabels();
    getValues();
  }, []);

  const charBarClicked = (colRef) => {
    console.log(colRef);
    //the index of the graphs keeps on increasing.
    //graphIndex divides by 4 and gets the riminder
    const graphIndex = colRef[0] % 4;
    const filArr = [];
    const eleArr = [];
    if (graphIndex === 0) {
      const profilter = projects
        .filter((pro) => pro.activity_education === colRef[2] + 1)
        .map((it) => {
          filArr.push(it.id);
        });
    }
    if (graphIndex === 1) {
      const profilter = projects
        .filter((pro) => pro.activity_health === colRef[2] + 1)
        .map((it) => {
          filArr.push(it.id);
        });
    }
    if (graphIndex === 2) {
      const profilter = projects
        .filter((pro) => pro.activity_development === colRef[2] + 1)
        .map((it) => {
          filArr.push(it.id);
        });
    } else {
      const profilter = projects
        .filter((pro) => pro.activity_jpi === colRef[2] + 1)
        .map((it) => {
          filArr.push(it.id);
        });
    }
    refs.current.map((item, index) => {
      eleArr.push(parseInt(item.id));
      refs.current[index].style.border = "solid black 1px"; //reset all borders to black
    });
    eleArr.map((item, index) => {
      if (filArr.includes(item)) {
        refs.current[index].style.border = "solid red 3px";
      }
    });
  };

  const addToRefs = (el) => {
    if (el && !refs.current.includes(el)) {
      refs.current.push(el);
    }
  };

  const radarLabels = [
    "Alternativa",
    "Decostruire",
    "Empowerment",
    "Misericordia",
  ];

  const radarValues = getAverage(projects);

  return (
    <Container>
      {projects.length > 0 && labels.length > 0 && values.length > 0 ? (
        <Row>
          <Col lg={4}>
            <h1>{province}</h1>
            <div>Progetti documentati: {projects.length}</div>
            <div>
              <LegendLeaders />
            </div>
            {/* <div refschart={addToRefCharts}>a</div>
            <div refschart={addToRefCharts}>b</div>
            <div refschart={addToRefCharts}>c</div> */}
            <div>
              {projects.map((item, index) => (
                <div key={item.id}>
                  <Link
                    //style={Styles.titleboxproject}
                    style={getColors(item.leader)}
                    className="hoverbox"
                    ref={addToRefs}
                    id={item.id}
                    to={`/project/${item.id}`}
                  >
                    {item.name}
                  </Link>
                  <br />
                </div>
              ))}
            </div>
          </Col>

          <Col>
            <Row>
              <Col>
                <ChartjsBar
                  dataGraph={[labels[0], values[0]]}
                  ctp={charBarClicked}
                  idchart={0} //give and id to the chart, to get it back in colRef
                />
              </Col>
              <Col>
                <ChartjsBar
                  dataGraph={[labels[1], values[1]]}
                  ctp={charBarClicked}
                  idchart={1}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <ChartjsBar
                  dataGraph={[labels[2], values[2]]}
                  ctp={charBarClicked}
                  idchart={2}
                />
              </Col>
              <Col>
                <ChartjsBar
                  dataGraph={[labels[3], values[3]]}
                  ctp={charBarClicked}
                  idchart={3}
                />
              </Col>
            </Row>
            <Row>
              <Col md={{ span: 6, offset: 6 }}>
                <RadarGraph
                  radarLabels={radarLabels}
                  radarValues={radarValues}
                />
              </Col>
            </Row>
            <Row>
              <Col lg={6} span={4} offset={6}></Col>
            </Row>
          </Col>
        </Row>
      ) : null}
    </Container>
  );
}

export default Province;
