import React, { useState } from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar } from "react-chartjs-2";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

function ChartjsBar({ dataGraph, ctp, idchart }) {
  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: "Chart.js Bar Chart",
      },
    },
    onClick: function (evt, element) {
      if (element.length > 0) {
        const chartindex = evt.chart.id;
        const series = element[0].datasetIndex;
        const index = element[0].index;
        const colRef = [idchart, series, index];
        ctp(colRef);
      }
    },
  };

  const labels = dataGraph[0];

  const data = {
    labels,
    datasets: [
      {
        label: "Dataset 1",
        data: dataGraph[1],
        backgroundColor: "rgba(255, 99, 132, 0.5)",
      },
    ],
  };

  return (
    <div>
      <Bar options={options} data={data} />
    </div>
  );
}

export default ChartjsBar;
