import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import { Routes, Route, Link, useParams } from "react-router-dom";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Table from "react-bootstrap/Table";

import { getColors, getAverage } from "../config/myfunctions";
import eventService from "../api/eventService";
import Styles from "../config/styles";
import routes from "../api/routes";
import RadarGraph from "../components/RadarGraph";
import ChartjsBar from "../components/ChartjsBar";
import ListPro from "../components/ListPro";
import LegendLeaders from "../components/LegendLeaders";

function AreaGeo() {
  const params = useParams();
  const area = params.id;
  const namearea = params.name;
  const [projects, setProjects] = useState([]);
  const [provinces, setProvinces] = useState([]);
  const [colRef, setColRef] = useState([]);
  const [labels, setLabels] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function getData() {
      await eventService.maproutes
        .getprojects(`?circumscription__cir_group=${area}&limit=50`)
        .then((res) => {
          setProjects(res.data.results);
          setLoading(false);
        })
        .catch((err) => {
          console.log(err);
        });
      await eventService.maproutes
        .getcirs(area)
        .then((res) => {
          setProvinces(res.data.circumscriptions);
        })
        .catch((err) => {
          console.log(err);
        });
      await eventService.maproutes
        .labels()
        .then((res) => {
          setLabels(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
      if (projects.length > 0 && labels.length > 0 && provinces.length > 0) {
        setLoading(false);
      }
    }
    getData();
  }, []);

  const charBarClicked = (colRef) => {
    // endOfPage.current.scrollIntoView({ behavior: "smooth" });
    // setShowListPro(true);
    setColRef(colRef);
  };

  const tg1 = [];
  const tg2 = [];
  const tg3 = [];
  const tg4 = [];
  if (projects.length > 0 && labels.length > 0 && provinces.length > 0) {
    var tglbl = [];
    var tgval = [];
    labels[0].edu_labels.map((item, index) => {
      const npro = projects.filter(
        (pro) => pro.activity_education === index
      ).length;
      tglbl.push(item.name);
      tgval.push(npro);
    });
    tg1.push(tglbl);
    tg1.push(tgval);

    tglbl = [];
    tgval = [];
    labels[1].hea_labels.map((item, index) => {
      const npro = projects.filter(
        (pro) => pro.activity_health === index
      ).length;
      tglbl.push(item.name);
      tgval.push(npro);
    });
    tg2.push(tglbl);
    tg2.push(tgval);

    tglbl = [];
    tgval = [];
    labels[2].dev_labels.map((item, index) => {
      const npro = projects.filter(
        (pro) => pro.activity_development === index
      ).length;
      tglbl.push(item.name);
      tgval.push(npro);
    });
    tg3.push(tglbl);
    tg3.push(tgval);

    tglbl = [];
    tgval = [];
    labels[3].jpi_labels.map((item, index) => {
      const npro = projects.filter((pro) => pro.activity_jpic === index).length;
      tglbl.push(item.name);
      tgval.push(npro);
    });
    tg4.push(tglbl);
    tg4.push(tgval);
  }

  const cirinpro = (pro) => {
    const arr = [];
    pro.circumscription.map((item, index) => {
      arr.push(item.id);
    });
    // console.log(arr);
    return arr;
  };

  const getProCountInCir = (cir, dim) => {
    let res = [];
    if (dim === "Picola") {
      res = projects.filter(
        (pro) => cirinpro(pro).includes(cir.id) && pro.dimension === "Picola"
      ).length;
    } else if (dim === "Media") {
      res = projects.filter(
        (pro) => cirinpro(pro).includes(cir.id) && pro.dimension === "Media"
      ).length;
    } else if (dim === "Grande") {
      res = projects.filter(
        (pro) => cirinpro(pro).includes(cir.id) && pro.dimension === "Grande"
      ).length;
    } else if (dim === "Indefinita") {
      res = projects.filter(
        (pro) =>
          cirinpro(pro).includes(cir.id) && pro.dimension === "Indefinita"
      ).length;
    } else {
      res = projects.filter((pro) => cirinpro(pro).includes(cir.id)).length;
    }
    return res;
  };

  const radarLabels = [
    "Alternativa",
    "Decostruire",
    "Empowerment",
    "Misericordia",
  ];

  const radarValues = getAverage(projects);

  return (
    <Container>
      {loading && <div>Loading</div>}
      <div style={{ position: "absolute", top: 70, right: 30 }}>
        <LegendLeaders />
      </div>
      <Row>
        <Col>
          Area geografica: {namearea} - {area}
        </Col>
      </Row>
      <Row>
        <Col>
          {provinces.map((item) => (
            <Link
              style={getColors([{ id: item.cir_leader.id }])}
              key={item.id}
              to={`/cir/${item.id}`}
            >
              {item.name}
            </Link>
          ))}
        </Col>
      </Row>
      <Row>
        <Col>
          <ChartjsBar dataGraph={tg1} ctp={charBarClicked} idchart={0} />
        </Col>
        <Col>
          <ChartjsBar dataGraph={tg2} ctp={charBarClicked} idchart={1} />
        </Col>
      </Row>
      <Row>
        <Col>
          <ChartjsBar dataGraph={tg3} ctp={charBarClicked} idchart={2} />
        </Col>
        <Col>
          <ChartjsBar dataGraph={tg4} ctp={charBarClicked} idchart={3} />
        </Col>
      </Row>
      <Row>
        <Col lg={8}>
          <Table striped bordered hover>
            <thead>
              <tr>
                <th></th>
                <th>Piccola</th>
                <th>Media</th>
                <th>Grande</th>
                <th>Indefinita</th>
                <th>Tot</th>
              </tr>
            </thead>
            <tbody>
              {provinces.map((cir) => (
                <tr key={cir.id}>
                  <td>{cir.name}</td>
                  <td>{getProCountInCir(cir, "Picola")}</td>
                  <td>{getProCountInCir(cir, "Media")}</td>
                  <td>{getProCountInCir(cir, "Grande")}</td>
                  <td>{getProCountInCir(cir, "Indefinita")}</td>
                  <td>{getProCountInCir(cir, "Tot")}</td>
                </tr>
              ))}
            </tbody>
          </Table>
        </Col>
        <Col>
          {" "}
          <RadarGraph radarLabels={radarLabels} radarValues={radarValues} />
        </Col>
      </Row>
    </Container>
  );
}

export default AreaGeo;
