import React, { useState, useEffect } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { Routes, Route, Link, useParams } from "react-router-dom";

import Styles from "../config/styles";
import eventService from "../api/eventService";

function ListPro({ colRef, gc }) {
  const [proList, setProList] = useState([]);

  useEffect(() => {
    const getData = () => {
      eventService.maproutes
        //colRef[0] ('edu', 'hea'...) colRef[2] (column clicked)
        .proinact(gc, colRef[0], colRef[2] + 1)
        .then((res) => {
          setProList(res.data.results);
        })
        .catch((err) => {
          console.log(err);
        });
    };
    getData();
  }, [colRef]); //render on change of colRef

  return (
    <div>
      <div>List of Projects...</div>
      {proList.length > 0
        ? proList.map((pro, index) => (
            <Link
              style={Styles.titleboxproject}
              className="hoverbox"
              key={pro.id}
              to={`/project/${pro.id}`}
            >
              {pro.name}
            </Link>
          ))
        : null}
    </div>
  );
}

export default ListPro;

