import React, { useState, useEffect } from "react";
import axios from "axios";
import { Button } from "react-bootstrap";

import ConfigApi from "../api/routes";

function GetCountries() {
  const [posts, setPosts] = useState([]);
  const [postsFive, setPostsFive] = useState([]);
  const [offset, setOffset] = useState(10);

  //   const [loading, setLoading] = useState(true);
  //   const [error, setError] = useState("");

  useEffect(() => {
    axios
      .get(ConfigApi.base_url + "countries")
      .then((res) => {
        setPosts(res.data.results);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const getCountriesOnClick = () => {
    setOffset(offset + 5);
    axios
      .get(ConfigApi.base_url + `countries/?limit=5&offset=${offset}`)
      .then((res) => {
        setPostsFive(res.data.results);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div>
      <h1>This is the list of the Countries</h1>
      <Button onClick={getCountriesOnClick}>Get countries</Button>
      <h3>Offset {offset}</h3>
      <h3>Five Country:</h3>
      <ul>
        {postsFive.map((post) => (
          <li key={post.id}>{post.name}</li>
        ))}
      </ul>

      <hr />
      <ul>
        {posts.map((post) => (
          <li key={post.id}>{post.name}</li>
        ))}
      </ul>
    </div>
  );
}

export default GetCountries;
