//here we are importing our Axios dependency
import axios from "axios";

//here is where we are defining our custom axios instance.
//if all of your API routes come from the same location, or you are using a web proxy to hit the server, you can provide a base url
//you can also attach axios interceptors to custom axios instances as well
const apiClient = axios.create({
  baseURL: "https://unthawing-booby-6013.dataplicity.io/",
  //baseURL: "http://192.168.0.171",
});

//Now set up the routes.  We are going to export a default object with keys that keep our API routes organized.  For example, all of the auth routes live in the Auth object

export default {
  maproutes: {
    getprojects(params) {
      return apiClient.get(`/projects/${params}`);
    },
    getcirs(cirgroup) {
      return apiClient.get(`/cir-group/${cirgroup}`);
    },
    circumscription(id) {
      return apiClient.get(`/circumscriptions/${id}`);
    },
    labels() {
      return apiClient.get("/labels");
    },
    proincir(pro) {
      return apiClient.get(`/projects/?circumscription=${pro}`);
    },
    proincirgroup(cirgroup) {
      return apiClient.get(`/projects/${cirgroup}/cirgroup`);
    },
    proinact(gc, act, index) {
      var colindex = ["", "", "", ""];
      if (act == 0) {
        colindex = [index, "", "", ""];
      } else if (act == 1) {
        colindex = ["", index, "", ""];
      } else if (act == 2) {
        colindex = ["", "", index, ""];
      } else {
        colindex = ["", "", "", index];
      }
      return apiClient.get(
        `/projects/?name=&circumscription__cir_group=${gc}&activity_education=${colindex[0]}&activity_health=${colindex[1]}&activity_jpic=${colindex[3]}&activity_development=${colindex[2]}&limit=100`
      );
    },
    project(id) {
      return apiClient.get(`/projects/${id}`);
    },
  },
  auth: {
    userLogin(payload) {
      return apiClient.post("/auth/login/", payload);
    },
  },
};
