import Colors from "../config/colors";
import Styles from "../config/styles";

export const plusSlides = (n) => {
  return n * 2;
};

export const getAverage = (arr) => {
  const res = [];
  const reducer1 = (total, currentObj) => total + currentObj.approach_mercy;
  const sum1 = arr.reduce(reducer1, 0);
  res.push(sum1 / arr.length);
  const reducer2 = (total, currentObj) =>
    total + currentObj.approach_empowerment;
  const sum2 = arr.reduce(reducer2, 0);
  res.push(sum2 / arr.length);
  const reducer3 = (total, currentObj) =>
    total + currentObj.approach_deconstructing;
  const sum3 = arr.reduce(reducer3, 0);
  res.push(sum3 / arr.length);
  const reducer4 = (total, currentObj) =>
    total + currentObj.approach_alternative;
  const sum4 = arr.reduce(reducer4, 0);
  res.push(sum4 / arr.length);
  return res;
};

