import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import { Routes, Route, Link, useParams } from "react-router-dom";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import eventService from "../api/eventService";
import Styles from "../config/styles";
import routes from "../api/routes";
import ChartjsBar from "../components/ChartjsBar";
import ListPro from "../components/ListPro";
import LegendLeaders from "../components/LegendLeaders";

function GroupCirs() {
  const endOfPage = useRef(null);
  const params = useParams();
  const gc = params.gc;
  const [dataFetch, setDataFetch] = useState([]);
  const [title, setTitle] = useState("");
  const [cirs, setCirs] = useState([]);
  const [tg1, setTg1] = useState([]);
  const [tg2, setTg2] = useState([]);
  const [tg3, setTg3] = useState([]);
  const [tg4, setTg4] = useState([]);
  const [showListPro, setShowListPro] = useState(false);
  const [colRef, setColRef] = useState([]);

  useEffect(() => {
    const getData = () => {
      eventService.maproutes
        .proincirgroup(gc)
        .then((res) => {
          setTitle(res.data[0].name.name);
          setCirs(res.data[1][0].cirs);
          setTg1(makeDataGraph(res.data, "edu"));
          setTg2(makeDataGraph(res.data, "hea"));
          setTg3(makeDataGraph(res.data, "dev"));
          setTg4(makeDataGraph(res.data, "jpi"));
        })
        .catch((err) => {
          console.log(err);
        });
    };
    getData();
  }, []);

  const makeDataGraph = (data, sec) => {
    var dataset = [];
    var lbl = "edu";
    var area = 0;
    var index = 0;
    if (sec == "edu") {
      area = 2;
      index = 0;
      lbl = data[area].edu_labels;
    } else if (sec == "hea") {
      area = 3;
      index = 1;
      lbl = data[area].hea_labels;
    } else if (sec == "dev") {
      area = 4;
      index = 2;
      lbl = data[area].dev_labels;
    } else {
      area = 5;
      index = 3;
      lbl = data[area].jpi_labels;
    }
    const datarow = data[6].valsecs[index];
    var lblrow = [];
    lbl.map((lb) => {
      lblrow.push(lb.name);
    });
    return [lblrow, datarow];
  };
  const charBarClicked = (colRef) => {
    endOfPage.current.scrollIntoView({ behavior: "smooth" });
    setShowListPro(true);
    setColRef(colRef);
  };

  return (
    <Container>
      <div style={{ position: "absolute", top: 70, right: 30 }}>
        <LegendLeaders />
      </div>
      <Row className="text-center" style={{ marginTop: 10 }}>
        <Col>
          <div style={Styles.titlebox}>Title: {title}</div>
        </Col>
      </Row>

      <Row className="text-center">
        <Col>
          {cirs.map((cir) => (
            <Link
              style={Styles.titleboxcirgroup}
              key={cir.id}
              to={`/cir/${cir.id}`}
            >
              {cir.name}
            </Link>
          ))}
        </Col>
      </Row>
      <Row>
        <Col>
          <ChartjsBar dataGraph={tg1} ctp={charBarClicked} idchart={0} />
        </Col>
        <Col>
          <ChartjsBar dataGraph={tg2} ctp={charBarClicked} idchart={1} />
        </Col>
      </Row>
      <Row>
        <Col>
          <ChartjsBar dataGraph={tg3} ctp={charBarClicked} idchart={2} />
        </Col>
        <Col>
          <ChartjsBar dataGraph={tg4} ctp={charBarClicked} idchart={3} />
        </Col>
      </Row>
      <Row>
        <Col>
          {/* Wait for the props to arrive in order to render the component */}
          {showListPro && colRef.length > 0 && gc && endOfPage ? (
            <ListPro colRef={colRef} gc={gc} />
          ) : null}
        </Col>
        <div style={{ height: "100px" }}></div>
        <div ref={endOfPage}></div>
      </Row>
    </Container>
  );
}

export default GroupCirs;
