import React, { useState, useEffect, useRef } from "react";
import { Routes, Route, useParams } from "react-router-dom";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Image from "react-bootstrap/Image";
import Table from "react-bootstrap/Table";
import Carousel from "react-bootstrap/Carousel";
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from "chart.js";
import { Radar } from "react-chartjs-2";

import Color from "../config/colors";
import pin from "../img/pin.png";
import eventService from "../api/eventService";
import Styles from "../config/styles";

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
);

function Project(props) {
  const params = useParams();
  const id = params.id;
  const [project, setProject] = useState();
  const [pinArray, setPinArray] = useState([]);
  const [radar, setRadar] = useState([]);
  const [slider, setSlider] = useState([]);
  const eleRef = useRef();
  const eduRef1 = useRef(),
    eduRef2 = useRef(),
    eduRef3 = useRef(),
    eduRef4 = useRef(),
    eduRef5 = useRef();
  const heaRef1 = useRef(),
    heaRef2 = useRef(),
    heaRef3 = useRef(),
    heaRef4 = useRef(),
    heaRef5 = useRef();
  const jpiRef1 = useRef(),
    jpiRef2 = useRef(),
    jpiRef3 = useRef(),
    jpiRef4 = useRef(),
    jpiRef5 = useRef();
  const devRef1 = useRef(),
    devRef2 = useRef(),
    devRef3 = useRef(),
    devRef4 = useRef(),
    devRef5 = useRef();

  useEffect(() => {
    const getData = () => {
      eventService.maproutes
        .project(id)
        .then((res) => {
          setProject(res.data);
          setPinArray(getPinArray(res.data.pin));
          setActiveSecMin(
            res.data.activity_education,
            res.data.activity_health,
            res.data.activity_jpic,
            res.data.activity_development
          );
          setRadar([
            res.data.approach_alternative,
            res.data.approach_empowerment,
            res.data.approach_deconstructing,
            res.data.approach_alternative,
          ]);
          setSlider(makeImgArray(res.data.img));
        })
        .catch((err) => {
          console.log(err);
        });
    };
    getData();
  }, []);

  const setActiveSecMin = (edu, hea, jpi, dev) => {
    if (edu == 1) {
      eduRef1.current.className = "tableactivecell";
    } else if (edu == 2) {
      eduRef2.current.className = "tableactivecell";
    } else if (edu == 3) {
      eduRef3.current.className = "tableactivecell";
    } else if (edu == 4) {
      eduRef4.current.className = "tableactivecell";
    } else if (edu == 5) {
      eduRef5.current.className = "tableactivecell";
    }

    if (hea == 1) {
      heaRef1.current.className = "tableactivecell";
    } else if (hea == 2) {
      heaRef2.current.className = "tableactivecell";
    } else if (hea == 3) {
      heaRef3.current.className = "tableactivecell";
    } else if (hea == 4) {
      heaRef4.current.className = "tableactivecell";
    }

    if (jpi == 1) {
      jpiRef1.current.className = "tableactivecell";
    } else if (jpi == 2) {
      jpiRef2.current.className = "tableactivecell";
    } else if (jpi == 3) {
      jpiRef3.current.className = "tableactivecell";
    } else if (jpi == 4) {
      jpiRef4.current.className = "tableactivecell";
    } else if (jpi == 5) {
      jpiRef5.current.className = "tableactivecell";
    }

    if (dev == 1) {
      devRef1.current.className = "tableactivecell";
    } else if (dev == 2) {
      devRef2.current.className = "tableactivecell";
    } else if (dev == 3) {
      devRef3.current.className = "tableactivecell";
    } else if (dev == 4) {
      devRef4.current.className = "tableactivecell";
    }
  };

  const getPinArray = (pins) => {
    const pinList = pins.split("\r\n");
    var pinArrayMap = [];
    pinList.map((pin, index) => {
      const pinpair = pin.split(",");
      pinArrayMap.push({ id: index, x: pinpair[0], y: pinpair[1] });
    });
    return pinArrayMap;
  };

  const makeImgArray = (imgs) => {
    const imgsplit = imgs.split("\r\n");
    const imgArray = [];
    imgsplit.map((img, index) => {
      imgArray.push({ id: index, img: img });
    });
    return imgArray;
  };

  const dataradar = {
    labels: ["Alternativa", "Decostruire", "Empowerment", "Misericordia"],
    datasets: [
      {
        label: "Approchio ministeriali",
        data: radar,
        backgroundColor: "rgba(255, 99, 132, 0.2)",
        borderColor: "rgba(255, 99, 132, 1)",
        borderWidth: 1,
      },
    ],
  };

  // https://www.chartjs.org/docs/latest/axes/
  const radaroptions = {
    scales: {
      r: {
        suggestedMin: 0,
        suggestedMax: 5,
        ticks: {
          stepSize: 1,
        },
      },
    },
    plugins: {
      legend: {
        display: false,
      },
    },
  };
  return (
    <Container fluid>
      {project && pinArray && slider ? (
        <>
          <Row style={{ marginTop: 10, marginRight: 15, marginLeft: 15 }}>
            <Col>
              <div style={Styles.titleProject}>{project.name}</div>
            </Col>
          </Row>

          <Row style={{ marginTop: 10, marginRight: 15, marginLeft: 15 }}>
            <Col lg={8}>
              <Row>
                <Col>
                  <div style={Styles.schedaProject}>
                    <div style={Styles.schedaProjectTitle}>Il progetto</div>
                    <div style={Styles.schedaProjectFields}>
                      <Row>
                        <Col lg={4} style={{ textAlign: "right" }}>
                          Leader:
                        </Col>
                        <Col>
                          {project.leader.map((item, index) => (
                            <span key={item.id}>
                              {(index ? ", " : "") + item.name}
                            </span>
                          ))}
                        </Col>
                      </Row>

                      <Row>
                        <Col lg={4} style={{ textAlign: "right" }}>
                          Circoscrizione:
                        </Col>
                        <Col>
                          {project.circumscription.map((item, index) => (
                            <span key={item.id}>
                              {(index ? ", " : "") + item.name}
                            </span>
                          ))}
                        </Col>
                      </Row>

                      <Row>
                        <Col lg={4} style={{ textAlign: "right" }}>
                          Paesi:
                        </Col>
                        <Col>
                          {project.country.map((item, index) => (
                            <span key={item.id}>
                              {(index ? ", " : "") + item.name}
                            </span>
                          ))}
                        </Col>
                      </Row>

                      <Row>
                        <Col lg={4} style={{ textAlign: "right" }}>
                          Luogo:
                        </Col>
                        <Col>{project.place}</Col>
                      </Row>

                      <Row style={{ marginTop: 15 }}>
                        <Col lg={4} style={{ textAlign: "right" }}>
                          Durata:
                        </Col>
                        <Col>{project.duration}</Col>
                      </Row>

                      <Row>
                        <Col lg={4} style={{ textAlign: "right" }}>
                          Dimensione:
                        </Col>
                        <Col>{project.dimension}</Col>
                      </Row>
                    </div>
                  </div>
                </Col>
                <Col>
                  <div style={Styles.schedaProject}>
                    <div style={Styles.schedaProjectTitle}>Carisma</div>
                    <div style={Styles.schedaProjectFields}>
                      <ul style={{ height: 142 }}>
                        {project.carisma.map((item, index) => (
                          <li key={item.id}>{item.name}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </Col>
              </Row>
              <Row>
                <Col style={Styles.abstract}>
                  <h4>Abstract</h4>
                  <div>{project.abstract}</div>
                </Col>
              </Row>
            </Col>
            <Col>
              <Row>
                <Col style={Styles.map}>
                  <Image style={{ borderRadius: 15 }} src={project.map} fluid />
                  {pinArray.map((item, index) => (
                    <div
                      key={item.id}
                      style={{
                        display: "inline-block",
                        position: "absolute",
                        top: item.y - 7 + "%",
                        left: item.x - 3 + "%",
                      }}
                    >
                      <Image src={pin} width={30} />
                    </div>
                  ))}
                </Col>
              </Row>
              <Row style={{ marginTop: 10 }}>
                <Col>
                  <div style={Styles.schedaProject}>
                    <div style={Styles.schedaProjectTitle}>
                      Obiettivi di sviluppo sostenibile
                    </div>
                    <div style={Styles.schedaProjectFields}>
                      <ul>
                        {project.sustainable_dev_obj.map((item, index) => (
                          <li key={item.id}>{item.name}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </Col>
              </Row>
              <Row style={{ marginTop: 10 }}>
                <Col>
                  <div style={Styles.schedaProject}>
                    <div style={Styles.schedaProjectTitle}>Actività</div>
                    <div style={Styles.schedaProjectFields}>
                      <ul>
                        {project.activity.map((item, index) => (
                          <li key={item.id}>{item.name}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </Col>
              </Row>
            </Col>
          </Row>
          <Row>
            <Col>
              <div style={Styles.titleProject2}>Settore Ministeriale</div>
            </Col>
          </Row>
          <Row style={{ marginTop: 10 }}>
            <Col style={Styles.tablesecmin}>
              <Table striped bordered hover>
                <tbody style={{ textAlign: "center" }}>
                  <tr style={Styles.tablelineodd}>
                    <td style={Styles.tablesecminleft}>Educazione</td>
                    <td ref={eduRef1}>Primaria</td>
                    <td ref={eduRef2}>Secondaria</td>
                    <td ref={eduRef3}>Terziaria</td>
                    <td ref={eduRef4}>Informale</td>
                    <td ref={eduRef5}>Pastorale giovanile</td>
                  </tr>
                  <tr style={Styles.tablelineeven}>
                    <td style={Styles.tablesecminleft}>Salute</td>
                    <td ref={heaRef1}>Dispensari/ospedali</td>
                    <td ref={heaRef2}>Salute sul territorio</td>
                    <td ref={heaRef3}>Riabilitazione</td>
                    <td ref={heaRef4}>Psicologica, mentale</td>
                    <td></td>
                  </tr>
                  <tr style={Styles.tablelineodd}>
                    <td style={Styles.tablesecminleft}>GPIC</td>
                    <td ref={jpiRef1}>Diritti umani</td>
                    <td ref={jpiRef2}>Ecologia, ambiente</td>
                    <td ref={jpiRef3}>Pace e riconciliazione</td>
                    <td ref={jpiRef4}>Giustizia sociale, inclusione</td>
                    <td ref={jpiRef5}>Dialogo inter-religioso</td>
                  </tr>
                  <tr style={Styles.tablelineeven}>
                    <td style={Styles.tablesecminleft}>Sviluppo</td>
                    <td ref={devRef1}>Soccorso</td>
                    <td ref={devRef2}>Sviluppo socio-economico</td>
                    <td ref={devRef3}>Governance, advocacy</td>
                    <td ref={devRef4}>Formazione di leader</td>
                    <td></td>
                  </tr>
                </tbody>
              </Table>
            </Col>
          </Row>
          <Row style={{ marginTop: 10, marginRight: 15, marginLeft: 15 }}>
            <Col>
              <div style={Styles.schedaProject}>
                <div style={Styles.schedaProjectTitle}>Bacino Sociale</div>
                <div style={Styles.schedaProjectFields}>
                  <ul>
                    {project.social_field.map((item, index) => (
                      <li key={item.id}>{item.name}</li>
                    ))}
                  </ul>
                </div>
              </div>
            </Col>
            <Col>
              <div style={Styles.schedaProject}>
                <div style={Styles.schedaProjectTitle}>Gruppi umani</div>
                <div style={Styles.schedaProjectFields}>
                  <ul>
                    {project.human_groups.map((item, index) => (
                      <li key={item.id}>{item.name}</li>
                    ))}
                  </ul>
                </div>
              </div>
            </Col>
          </Row>
          <Row
            style={{
              marginTop: 10,
              marginRight: 15,
              marginLeft: 15,
            }}
          >
            <Col>
              <div style={Styles.schedaProject}>
                <div style={Styles.schedaProjectTitle}>
                  Collaborazioni ministeriali
                </div>
                <div style={Styles.schedaProjectFields}>
                  <ul>
                    {project.coordinating_team.map((item, index) => (
                      <li key={item.id}>{item.name}</li>
                    ))}
                  </ul>
                </div>
              </div>
            </Col>
            <Col>
              <div style={Styles.schedaProject}>
                <div style={Styles.schedaProjectTitle}>
                  Articolazioni ministeriali
                </div>
                <div style={Styles.schedaProjectFields}>
                  <ul>
                    {project.main_actors.map((item, index) => (
                      <li key={item.id}>{item.name}</li>
                    ))}
                  </ul>
                </div>
              </div>
            </Col>
            <Col>
              <div style={Styles.schedaProject}>
                <div style={Styles.schedaProjectTitle}>
                  Dimensione ecclesiale
                </div>
                <div style={Styles.schedaProjectFields}>
                  <div>
                    <b>Rapporto con la chiesa locale</b>
                  </div>
                  <div style={{ marginLeft: 10 }}>
                    {project.church_relation.name}
                  </div>
                  <div style={{ marginTop: 10 }}>
                    <b>Modello ecclesiale</b>
                  </div>
                  <div style={{ marginLeft: 10 }}>{project.church_model}</div>
                </div>
              </div>
            </Col>
          </Row>
          <Row
            style={{
              marginTop: 20,
              marginLeft: 25,
              marginRight: 15,
              marginBottom: 30,
            }}
          >
            <Col lg={4}>
              <Radar data={dataradar} options={radaroptions} />
            </Col>
            <Col>
              <Carousel>
                {slider.map((item, index) => (
                  <Carousel.Item key={item.id}>
                    <img style={Styles.imgcarousel} src={item.img} />
                  </Carousel.Item>
                ))}
              </Carousel>
            </Col>
          </Row>
        </>
      ) : null}
    </Container>
  );
}

export default Project;
