import React, { useState } from "react";
import Child from "./Child";
// import Button from "react-bootstrap/Button";
import { Button } from "react-bootstrap"; //for several components

import Seeleaders from "./Seeleaders";
import eventService from "../api/eventService";
import useFetchData from "../api/use-fetch-data";

function Parent() {
  const [data2, setData2] = useState("data to child");
  const [dataC, setDataC] = useState("");
  const [dataApi, setDataApi] = useState([]);
  const [showLeaders, setShowLeaders] = useState(false);

  const [urlPoint, setUrlPoint] = useState(
    "https://unthawing-booby-6013.dataplicity.io/cir-group/"
  );
  const { data, loading, url } = useFetchData(urlPoint);

  const parentToChild = (x) => {
    setData2(`This is data from Parent to Child ${x}`);
    eventService.maproutes
      .getcirs(x)
      .then((res) => {
        // console.log(res.data.circumscriptions);
        console.log("hi");
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const childToParent = (childdata) => {
    setDataC(childdata);
  };

  return (
    <div>
      <h3>This is parent: {data2}</h3>
      <h3>This is data from child: {dataC}</h3>
      <Button onClick={setShowLeaders}>test API</Button>
      <Button onClick={() => parentToChild(3)}>Click Parent</Button>
      <Child ptc={data2} ctp={childToParent} />
      {/* <p>Data: {data}</p> */}
      <div>
        {showLeaders && <Seeleaders />}

        {loading && <h1>Loading</h1>}
        {!loading && (
          <div>
            <h4>Doing stuff with data</h4>
            <h5>Url: {url}</h5>
            <ul>
              {data.map((item) => (
                <li>{item.name}</li>
              ))}
            </ul>
          </div>
        )}
      </div>
    </div>
  );
}

export default Parent;
